@import"https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap";*{margin:0;padding:0}body{background:#fff;color:#000;font-family:"Poppins",sans-serif;font-weight:300}.wrapper{width:1280px;margin:50px auto}header{position:relative}header .logo{font-weight:600;font-size:40px;cursor:pointer}header .presentation{margin:50px auto;background:url("./img/shopClub.jpg") no-repeat;width:100%;height:500px;background-size:cover;background-position:center center;background-blend-mode:multiply;background-color:#6d6d6d;position:relative}header .presentation::after{content:"Лучшая одежда для вас";position:absolute;padding-left:50px;top:100px;left:0px;width:300px;font-size:40px;font-weight:600;color:#fff}header .presentation::before{content:"По низким ценам";position:absolute;padding-left:50px;top:300px;left:0px;width:300px;font-size:20px;font-weight:300;color:#fff}header ul.nav{margin:20px auto;float:right;list-style:none}header ul.nav li{display:inline;margin-left:25px;cursor:pointer;transition:opacity 500ms ease}header ul.nav li:hover{opacity:.7}header .shop-cart-button{float:right;cursor:pointer;transition:color,transform 500ms ease;padding-top:20px;height:25px;width:25px}header .shop-cart-button:hover,header .shop-cart-button.active{color:#ec4545}header .shop-cart-button.active{transform:scale(1.3)}header .shop-cart{position:absolute;top:75px;right:0;width:450px;padding:20px;background:#eeeded;-webkit-box-shadow:16px 4px 12px 8px rgba(34,60,80,.13);-moz-box-shadow:16px 4px 12px 8px rgba(34,60,80,.13);box-shadow:16px 4px 12px 8px rgba(34,60,80,.13);z-index:9}header .shop-cart .item_cart{width:100%;float:left;margin-bottom:20px}header .shop-cart .item_cart img{width:70px;float:left;margin-right:20px}header .shop-cart .item_cart h3{font-size:20px;margin-bottom:10px}header .shop-cart .item_cart p{font-weight:300}header .shop-cart .empty h2{font-size:20px;margin-bottom:20px}main{display:flex;width:100%;flex-wrap:wrap;justify-content:space-between}main .item{width:30%;margin-bottom:50px;overflow:hidden;position:relative;padding-bottom:20px;border-radius:10px 10px 0px 0px;transition:background 500ms ease}main .item:hover{background:#ddd}main .item img{width:100%;transition:transform 500ms ease;border-radius:10px 10px 0px 0px;height:500px}main .item img:hover{transform:scale(1.03)}main h3,main p,main b{margin:10px 20px;color:#333}main .add-to-cart{position:absolute;right:20px;bottom:20px;background:#e35e00;height:35px;width:35px;border-radius:50%;text-align:center;line-height:35px;color:#fff;cursor:pointer;font-weight:600;font-size:20px;transition:transform 500ms ease}main .add-to-cart:hover{transform:scale(1.5) translateY(-5px)}footer{text-align:center;padding-top:10px}.bestsellers_title{text-align:center;margin:30px 0px}